@import "../../assets/style/app";

.app {
  background: #aae0ff;
  background-image: url(../../assets/png/background.png);
  background-size: cover;
  min-height: 100vh;
  overflow: hidden;
  position: relative;
  padding-top: 50px;
  padding-bottom: 16px;

  @include desktop {
    padding-top: 0;
    padding-bottom: 50px;
  }

  .head {
    position: relative;
    z-index: 1;
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 212px;
    height: auto;

    @include desktop {
      width: 788px;
    }
  }

  .doSomething {
    position: absolute;
    z-index: 2;
    display: block;
    width: 233px;
    height: auto;
    left: 50%;
    transform: translateX(-50%);
    top: 180px;

    @include desktop {
      width: 866px;
      top: 500px;
    }

  }

  .beavo {
    position: absolute;
    z-index: 2;
    display: block;
    left: 50%;
    transform: translateX(-50%);
    width: 304px;
    height: auto;
    top: 240px;

    @include desktop {
      width: 1131px;
      top: 680px;
    }
  }

  .plakat {
    position: absolute;
    z-index: 2;
    display: block;
    left: calc(50% + 145px);
    transform: translateX(-50%);
    width: 136px;
    height: auto;
    top: 104px;

    @include desktop {
      left: calc(50% + 500px);
      width: 507px;
      top: 200px;
    }
  }

  .mouth {
    position: absolute;
    z-index: 1;
    display: block;
    left: calc(50% - 145px);
    transform: translateX(-50%);
    width: 118px;
    height: auto;
    top: 178px;

    @include desktop {
      left: calc(50% - 500px);
      width: 440px;
      top: 475px;
    }
  }

  .mouth2 {
    position: absolute;
    z-index: 1;
    display: block;
    left: calc(50% - 100px);
    transform: translateX(-50%);
    width: 37px;
    height: auto;
    top: 176px;

    @include desktop {
      left: calc(50% - 340px);
      width: 140px;
      top: 468px;
    }
  }

  .hand {
    position: absolute;
    z-index: 1;
    display: block;
    left: calc(50% + 150px);
    transform: translateX(-50%);
    width: 150px;
    height: auto;
    top: 250px;

    @include desktop {
      left: calc(50% + 550px);
      width: 558px;
      top: 745px;
    }
  }

  .cloud {
    position: absolute;
    z-index: 1;
    display: block;
    left: calc(50% + 30px);
    transform: translateX(-50%);
    width: 278px;
    height: auto;
    top: 173px;

    @include desktop {
      left: calc(50% + 130px);
      width: 1034px;
      top: 456px;
    }
  }

  .blur {
    position: absolute;
    z-index: 1;
    display: block;
    left: calc(50%);
    transform: translateX(-50%);
    width: 232px;
    height: 232px;
    top: 60px;
    background: radial-gradient(rgba(255, 255, 255, 1), rgba(255, 255, 255, 0) 75%);
    border-radius: 50%;
    //backdrop-filter: blur(18px);

    @include desktop {
      width: 1440px;
      height: 1440px;
      top: 30px;
    }
  }

  .socialLinks {
    position: absolute;
    z-index: 2;
    left: calc(50% - 164px);
    transform: translateX(-50%);
    top: 44px;

    @include desktop {
      top: 147px;
      left: calc(50% - 564px);
    }

    a:nth-child(1), a:nth-child(1) img {
      display: block;
      width: 47px;
      height: 46px;

      @include desktop {
        width: 141px;
        height: 136px;
      }
    }

    a:nth-child(2), a:nth-child(2) img {
      display: block;
      width: 42px;
      height: 42px;

      @include desktop {
        width: 124px;
        height: 124px;
      }
    }

    a:nth-child(2) {
      margin-top: 10px;

      @include desktop {
        margin-top: 54px;
      }
    }

    a {
      transition: 0.3s;

      &:hover {
        opacity: 0.75;
      }
    }

  }

  .text {
    position: relative;
    z-index: 2;
    font-size: 24px;
    text-align: center;
    color: #2b2847;
    @include text_shadow_white_4;

    @include desktop {
      font-size: 59px;
      @include text_shadow_white_9;
    }

    &_0 {
      margin-top: 132px;

      @include desktop {
        margin-top: 490px;
      }
    }
  }

  .ca {
    position: relative;
    z-index: 2;
    border: 3px solid #2b2847;
    border-radius: 16px;
    width: 327px;
    background: #fff;
    color: #2b2847;
    padding: 6px;
    margin-top: 24px;
    word-break: break-all;
    margin-left: auto;
    margin-right: auto;
    text-align: center;

    @include desktop {
      border-radius: 42px;
      width: 927px;
      padding: 20px;
      margin-top: 32px;
      font-size: 59px;
    }
  }

  .videoWrapper {
    cursor: pointer;
    @include centerRow;
    position: relative;
    z-index: 2;
    width: 222px;
    height: 392px;
    background: #242424;
    margin-top: 42px;
    margin-left: auto;
    margin-right: auto;

    @include desktop {
      width: 508px;
      height: 897px;
      margin-top: 62px;
    }

    svg {
      position: absolute;
      @include absoluteCenter;
      width: 60px;
      height: 60px;

      @include desktop {
        width: 180px;
        height: 180px;
      }
    }

    video {
      opacity: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    &_play {
      svg {
        display: none;
      }

      video {
        opacity: 1;
      }
    }
  }

  .texts {
    margin-top: 40px;
    width: 336px;
    margin-left: auto;
    margin-right: auto;

    @include desktop {
      margin-top: 62px;
      width: 1150px;
    }

    .text {
      position: relative;
      z-index: 2;
      font-size: 24px;
      text-align: center;
      color: #2b2847;
      @include text_shadow_white_4;

      @include desktop {
        font-size: 59px;
        @include text_shadow_white_9;
      }

      &:not(:first-child) {
        margin-top: 24px;

        @include desktop {
          margin-top: 59px;
        }
      }
    }
  }

  .footer_logo {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 188px;
    height: auto;
    margin-top: 20px;

    @include desktop {
      width: 524px;
    }

  }

  .label {
    font-size: 24px;
    text-align: center;
    color: #2b2847;
    @include text_shadow_white_4;
    margin-top: 24px;

    @include desktop {
      font-size: 59px;
      @include text_shadow_white_9;
    }
  }

}